.modal{
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-color:rgba(0,0,0,0.5);
    display: flex;
    align-items: center;
    justify-content: center;
}
.modal-content{
    width: 500px;
    background-color: #fff;
}
.modal-header, .modal-footer{
    padding: 20px;
}

.modal-title{
    margin: 0;
}

.modal-body{
    padding: 20px;
    border-top: 1px solid #eee;
    border-bottom: 1px solid #eee;
}